:root {
    color-scheme: dark;

    --primary-50: #fbe9e7;
    --primary-100: #ffccbc;
    --primary-200: #ffab91;
    --primary-300: #ff8a65;
    --primary-400: #ff7043;
    --primary-500: #ff5722;
    --primary-600: #f4511e;
    --primary-700: #e64a19;
    --primary-800: #d84315;
    --primary-900: #bf360c;
    --primary-A100: #ff9e80;
    --primary-A200: #ff6e40;
    --primary-A400: #ff3d00;
    --primary-A700: #dd2c00;

    --secondary-50: #eceff1;
    --secondary-100: #cfd8dc;
    --secondary-200: #b0bec5;
    --secondary-300: #90a4ae;
    --secondary-400: #78909c;
    --secondary-500: #607d8b;
    --secondary-600: #546e7a;
    --secondary-700: #455a64;
    --secondary-800: #37474f;
    --secondary-900: #263238;
    --secondary-A100: #cfd8dc;
    --secondary-A200: #b0bec5;
    --secondary-A400: #78909c;
    --secondary-A700: #455a64;

    --unlistened: #374349;
}
body {
    user-select: none;
}
.radio-six {
    user-select: auto;
}
.body {
    max-width: 500px;
}

.rhap_container {
    background-color: #444 !important;
}

.rhap_time {
    color: white !important;
}


.player {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}

.date-navbar {
    position: fixed;
    top: 0;
    left: 140px;
    right: 0;
    height: 50px;
    padding: 10px 10px 0;
}

.freq-sidebar {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 20px;
    width: 140px;
    background: #121212;
    border-right: 1px solid #444;
    user-select: none;
}

.freq-sidebar-search {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background-color: #121212;
    z-index: 10;

}

.freq-sidebar-list {
    overflow-y: scroll;
    position: fixed;
    left: 0;
    top: 56px;
    bottom: 20px;
    width: 140px;
    user-select: none;
}
.freq-sidebar-list__empty {
    height: 56px;
    padding-top: 56px;
}

.freq-sidebar-row {
    width: 100%;
    padding: 2px 10px;
    border-bottom: 1px solid #444;
    position: relative;
}

.freq-sidebar-row:hover {
    cursor: pointer;
    border-bottom: 1px solid #fff;
}

.freq-sidebar-row_unlistened {
    background-color: var(--unlistened);
    border-color: #505f67;
}
.freq-sidebar-row_active {
    background-color: var(--primary-800);
    color: var(--secondary-50);
}

.freq-sidebar-row_selected:after {
    content: '';
    position: absolute;
    top: 0;right: 0;bottom: 0;width: 6px;
    background-color: var(--primary-800);
}
/*.freq-sidebar-row_selected:hover:after {*/
/*    display: none;*/
/*}*/


.freq-sidebar-row__chattitle {
    font-size: 9px;
    color: #baeceb;
    line-height: 1;
}
.freq-sidebar-row__lasttime {
    color: #e2e2e2;
}
.freq-sidebar-row__fq {
    line-height: 1.6;
    margin-bottom: 1px;
    /*margin-top: 2px*/
}
.freq-sidebar-row__badge {
    position: absolute;
    top: 50%;
    margin-top: -7px;
    font-size: 10px;
    line-height: 14px;
    right: 15px;
    background-color: #4a8784;
    height: 14px;
    min-width: 14px;
    padding: 0 4px;
    text-align: center;
    border-radius: 7px;
}

/* ------------------ ------------------ */

.content {
    /*margin-left: 140px;*/
    position: fixed;
    left: 140px;
    /*right: 0;*/
    /*bottom: 0;*/
    top: 0;
    bottom: 20px;
    width: 200px;
    border-right: 1px solid #000;
}


.content__top {
    height: 56px;
    background-color: rgba(255, 255, 255, 0.09);
    border-bottom: 1px solid #bfbfbf;
    /*padding: 10px 20px;*/
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: stretch;
}
.content__top h4 {
    margin: 0;
}
.content__top-controls {
    padding: 0 20px;
    flex: 1;
}
.content__top-scanners{

}
.scanners_panel {

}



.content__clean-btn {
    position: absolute;
    width: 6px; left: -3px; top: 0; bottom: 0;
    /*border-radius: 3px;*/
    background-color: #78909c;
    z-index: 10;
}
.content__clean-btn:hover {
    background-color: #90a4ae;
    cursor: pointer;
}
/* ------------------ ------------------ */

.recs-list {
    /*border-right: 1px solid #444;*/
    /*width: 220px;*/
    /*position: fixed;*/
    position: absolute;
    left: 0;
    top: 56px;
    bottom: 0;
    /*right: 0;*/
    width: 200px;
    /*margin-left: 10px;*/
    overflow-y: scroll;

}

.rec {
    position: relative;
    padding: 2px 0 2px 10px;
    margin: 0 10px 0 15px;
    cursor: pointer;
    /*background-color: #2b2b2b;*/
    border-bottom: 1px solid #444;
    user-select: none;
    /*font-weight: 300;*/
}
.rec-last {
    border-bottom: none;
}

.rec:hover {
    background-color: #1f1c1a;
    border-bottom-color: white;
}
.rec:hover .rec__scanner {
    background-color: white !important
}
.rec-unlistened {
    background-color: var(--unlistened);
}
.rec-playing {
    /*background-color: var(--primary-800) !important;*/
    /*color: var(--secondary-50) !important;*/
}
.rec-playing .rec__icon {
    display: block;
}



.rec__time {
    color: #9d9d9d;
}

.rec__time b {
    color: white;
    font-weight: normal;
}
.rec-voiceDetected .rec__time b {
    font-weight: bold;
}


.rec__duration {
    position: absolute;
    right: 10px;
    top: 1px;
    color: #cccccc;
}
.rec-noiseDetected .rec__time,
.rec-noiseDetected .rec__time b,
.rec-noiseDetected .rec__duration{
    color: #90a4ae;
}
/* ------------------ ------------------ */
.rec__icon svg {
    width: 17px;
}
.rec__icon {
    position: absolute;
    left: -9px;
    top: 2px;
    display: none;
}
.rec__scanner {
    position: absolute;
    bottom: -1px;
    height: 1px;
    width: 32%;
    right: 0;
}
/* ------------------ ------------------ */


.radio-six {
    position: fixed;
    left: 340px;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: #444;
}
/* ------------------ ------------------ */
.progressbar {
    position: fixed;
    bottom: 0; left: 0;
    width: 340px;
    height: 20px;
    background-color: #263238;
    user-select: none;
    cursor: text;

}
.progressbar__progress {
    /*height: 20px;*/
    /*width: 100%;*/
    position: absolute;
    top: 0; left: 0; right: 0; bottom: 0;
    background-color: var(--primary-800);
    will-change: transform;
    user-select: none;
    transition: transform 0.05s linear;
    /*transition: transform 2s linear;*/
}
.progressbar__progress-noanimation {
    will-change: none;
    transition: none !important;
    transform: translate3d(0,0,0);
}

/* ----------------------------------------- */
.scanners_panel {
    /*position: absolute;*/
    /*top: 15px;*/
    /*left: 18px;*/
    display: flex;
    justify-content: stretch;
    margin-left: 3px;
}
.uniden {
    height:18px;
    flex: 1;
    padding: 0 6px 0 0;
    text-align: center;
    position: relative;
    /*position: absolute;*/
    /*z-index: 15;*/
    /*left: 18px;*/
    /*padding: 2px 0 0 0;*/
}
.uniden__fq {
    color: white;
    font-weight: bold;
    font-size: 12px;
    line-height: 18px;
    font-family: "Courier New", serif;
    cursor: pointer;
    /*padding-right: 8px;*/
    /*position: absolute;*/
}
.uniden__fq_record:after {
    content: '';
    position: absolute;
    right: 3px;
    top: 50%;
    margin-top: -2.5px;
    background-color: white;
    width: 5px;
    height: 5px;
    border-radius: 2.5px;
}
.uniden__remote {
    position: absolute;
    z-index: 20;
    top: 18px;left: 0;right: 0;
    background-color: lightslategray;
}
.uniden__remote-list {}
.uniden__remote-item {
    font-size: 12.5px;
    line-height: 30px;
    border-bottom: 1px solid #cccccc;
}
.uniden__remote-item:hover {
    cursor: pointer;
    background-color: #4a8784;
}

.fq-apiconnect {
    width: 5px;
    height: 5px;
    position: absolute;
    z-index: 12;
    left: 7px;
    top: 7px;
    background-color: red;
}
.fq-apiconnect-connected {
    background-color: greenyellow;
}
.fq-lastRecordingFQ {
    position: absolute;
    left: 12px;
    top: 0;
    font-size: 14px;
    color: white;
    z-index: 13;
}